"use client";

import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { Check, Shield, Clock, Zap, Users, Layers } from 'lucide-react';
import { CSSProperties } from 'react';
import { featuresList } from '@/data/featuresList';

interface FeatureItemProps {
  icon: React.ReactNode;
  title: string;
  description: string;
  index: number;
}

const FeatureItem = ({ icon, title, description, index }: FeatureItemProps) => {
  const isEven = index % 2 === 0;
  
  const featureBoxStyle: CSSProperties = {
    padding: '2.5rem',
    backgroundColor: 'rgba(255, 255, 255, 0.02)',
    position: 'relative',
    overflow: 'hidden',
    backdropFilter: 'blur(5px)',
    borderRadius: '4px',
    margin: '0 auto',
    maxWidth: '1000px',
    display: 'flex',
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'space-between',
    border: '1px solid rgba(255, 255, 255, 0.05)',
    marginBottom: '2rem'
  };

  const iconContainerStyle: CSSProperties = {
    width: '4rem',
    height: '4rem',
    backgroundColor: 'rgba(255, 255, 255, 0.03)',
    borderRadius: '12px',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    border: '1px solid rgba(255, 255, 255, 0.08)',
    color: 'var(--primary)',
    transition: 'all 0.4s ease',
    flexShrink: 0,
    marginRight: '2rem'
  };

  const contentStyle: CSSProperties = {
    flex: 1
  };

  const tagStyle: CSSProperties = {
    display: 'inline-block',
    padding: '0.35rem 0.75rem',
    fontSize: '0.7rem',
    fontWeight: 600,
    textTransform: 'uppercase',
    letterSpacing: '0.1em',
    color: '#000',
    backgroundColor: 'var(--primary)',
    borderRadius: '4px',
    marginBottom: '1rem'
  };

  const titleStyle: CSSProperties = {
    fontSize: '1.5rem',
    fontWeight: 600,
    marginBottom: '1rem',
    fontFamily: 'var(--font-display)',
    letterSpacing: '0.02em'
  };

  const descriptionStyle: CSSProperties = {
    fontSize: '1rem',
    color: 'rgba(255, 255, 255, 0.7)',
    lineHeight: 1.7,
    fontWeight: 300
  };

  const overlayStyle: CSSProperties = {
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    background: 'linear-gradient(135deg, rgba(var(--primary-rgb), 0) 0%, rgba(var(--primary-rgb), 0.05) 100%)',
    opacity: 0,
    transition: 'opacity 0.4s ease, transform 0.4s ease',
    pointerEvents: 'none',
    zIndex: 0
  };

  return (
    <motion.div
      initial={{ opacity: 0, y: 30 }}
      whileInView={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.6, delay: index * 0.1 }}
      viewport={{ once: true, amount: 0.3 }}
      whileHover={{
        y: -5,
        transition: { duration: 0.3 }
      }}
    >
      <motion.div
        style={featureBoxStyle}
        whileHover={{
          borderColor: 'var(--primary)',
          boxShadow: '0 10px 30px rgba(var(--primary-rgb), 0.1)'
        }}
      >
        <motion.div
          style={overlayStyle}
          whileHover={{
            opacity: 1
          }}
        />

        <motion.div
          style={iconContainerStyle}
          whileHover={{
            borderColor: 'var(--primary)',
            transform: 'rotate(10deg)'
          }}
        >
          {icon}
        </motion.div>

        <div style={contentStyle}>
          <div style={tagStyle}>FEATURE</div>
          <h3 style={titleStyle}>{title}</h3>
          <p style={descriptionStyle}>{description}</p>
        </div>
      </motion.div>
    </motion.div>
  );
};

export default function Features() {
  const sectionStyle: CSSProperties = {
    backgroundColor: '#000',
    padding: '8rem 1.5rem',
    position: 'relative',
    borderTop: '1px solid rgba(255, 255, 255, 0.05)',
    borderBottom: '1px solid rgba(255, 255, 255, 0.05)'
  };

  const containerStyle: CSSProperties = {
    maxWidth: '1280px',
    margin: '0 auto'
  };

  const headerStyle: CSSProperties = {
    textAlign: 'center',
    marginBottom: '5rem'
  };

  const titleStyle: CSSProperties = {
    fontSize: 'clamp(2rem, 5vw, 3rem)',
    fontWeight: 700,
    marginBottom: '1.5rem',
    fontFamily: 'var(--font-display)',
    letterSpacing: '0.02em',
    textTransform: 'uppercase'
  };

  const subtitleStyle: CSSProperties = {
    fontSize: 'clamp(1rem, 3vw, 1.25rem)',
    lineHeight: 1.6,
    color: 'rgba(255, 255, 255, 0.7)',
    maxWidth: '700px',
    margin: '0 auto',
    fontWeight: 300
  };

  // Add an interesting background pattern for the section
  const backgroundStyle: CSSProperties = {
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    background: 'radial-gradient(circle at 20% 30%, rgba(var(--primary-rgb), 0.05) 0%, transparent 60%), radial-gradient(circle at 80% 70%, rgba(var(--primary-rgb), 0.03) 0%, transparent 40%)',
    zIndex: 0
  };

  return (
    <section id="features" style={sectionStyle}>
      <div style={backgroundStyle}></div>
      <div style={containerStyle}>
        <div style={headerStyle}>
          <motion.h2
            style={titleStyle}
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6 }}
            viewport={{ once: true, amount: 0.3 }}
          >
            Our Advanced Features
          </motion.h2>
          <motion.p
            style={subtitleStyle}
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6, delay: 0.2 }}
            viewport={{ once: true, amount: 0.3 }}
          >
            Discover how our cutting-edge solutions can transform your business with these powerful features designed for the modern enterprise.
          </motion.p>
        </div>

        <div style={{ position: 'relative', zIndex: 1 }}>
          {featuresList.map((feature, index) => (
            <FeatureItem
              key={index}
              icon={<feature.icon size={24} />}
              title={feature.title}
              description={feature.description}
              index={index}
            />
          ))}
        </div>
      </div>
    </section>
  );
} 